<template>
   <el-row class="trainingPC">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					数据仿真
   				</span>
   	  </el-col>
   <!-- 	  <el-col :span="2" :offset="20" class="more">
   	  		更多	  
   	  </el-col> -->
     </el-col>
	 <el-col class="searchCommon">
	 		 <span class="vimg spanCo">
	 			  <img src="../../assets/img/time.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">
	 			时间排序
	 		 </span>
	 		 <span class="vimg spanCo">
	 		 	  <img src="../../assets/img/px.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">数据仿真</span>
	 		 <el-col :span="4" class="imput">
	 		 <el-input placeholder="输入相关数据仿真" v-model="input">
	 		  </el-input>
	 		 </el-col>
	 		 <el-button type="primary" @click="search"><img src="../../assets/img/search.png" >搜索</el-button>
	 </el-col>
	 <el-col :span="24" class="bottom">
		 <div class="bbottom">
			 <div v-show="records.length==0">
			 	暂无数据
			 </div>
		 	<div class="comBox" v-for="(item,index) in records" :key="index">
		 		<div class="comTop">
		 		<!-- 	<div class="leftBtn">
		 				<div class="circleBig" @click="showC">
		 					<div class="circle" :class="{activeC:circleBig}">
		 					</div>
		 				</div>
		 			</div> -->
					<div class="leftBtnText">
						{{item.emuTitleName}}
					</div>
					<div class="rightText">
						发布时间:{{change(item.createTime)}}
					</div>
		 		</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		组织机构:<span class="bigText">{{item.orgName}}</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">{{item.emuCode}}</span>
					</div>
					<!-- <div class="">
						时长:<span class="bigText"></span>
					</div> -->
					<div class="">
						分类:<span class="bigText">{{item.emuType}}</span>
					</div>
				 </div>
				 <div class="startBtn" @click="dow(item)">
				 	开始下载
				 </div>
		 	</div>
			<div class="comBox" v-show="false">
				<div class="comTop">
					<div class="leftBtn">
						<div class="circleBig" @click="showC">
							<div class="circle" :class="{activeC:circleBig}">
							</div>
						</div>
					</div>
					<div class="leftBtnText">
						职业与创业胜任力胜任力…
					</div>
					<div class="rightText">
						发布时间:2021-09-08-12 12:12:23
					</div>
				</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		主讲人:<span class="bigText">张朝阳</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">01209131121</span>
					</div>
					<div class="">
						推演时间:<span class="bigText">2021-09-23-12 09:03:22</span>
					</div>
					<div class="">
						分类:<span class="bigText">理论</span>
					</div>
				 </div>
				 <div class="startBtn">
				 	开始下载
				 </div>
			</div>
			<div class="comBox" v-show="false">
				<div class="comTop">
					<div class="leftBtn">
						<div class="circleBig" @click="showC">
							<div class="circle" :class="{activeC:circleBig}">
							</div>
						</div>
					</div>
					<div class="leftBtnText">
						职业与创业胜任力胜任力…
					</div>
					<div class="rightText">
						发布时间:2021-09-08-12 12:12:23
					</div>
				</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		主讲人:<span class="bigText">张朝阳</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">01209131121</span>
					</div>
					<div class="">
						推演时间:<span class="bigText">2021-09-23-12 09:03:22</span>
					</div>
					<div class="">
						分类:<span class="bigText">理论</span>
					</div>
				 </div>
				 <div class="startBtn">
				 	开始下载
				 </div>
			</div>
		 </div>
	<!-- 	 <el-col :span="24">
			 <div class="circleBig fillS2" @click="showC">
			 	<div class="circle" :class="{activeC:circleBig}">
			 	</div>
			 </div>
			 全选
			 <div class="downBtn">
			 	批量下载
			 </div>
		 </el-col> -->
	 </el-col>
	 <el-col :span="24" class="cpage">
	 			 <el-pagination
	 			   background
	 				   :prev-text='prevT'
	 				   :next-text='nextT'
	 			   layout="prev, pager, next"
	 			   :total="total">
	 			 </el-pagination>
	 </el-col>
   </el-row>
</template>

<script>
import {emulation,dowlond}  from '../../api/apis/user.js'

import {parseTime,dowl} from '../../utils/utils.js'
export default {
	name:'trainingPC',
	data(){
		return {
			prevT:'上一页',
			nextT:'下一页',
			status:'进行中',
			status1:'',
			status2:'已完成',
			oldIndex:0,
			nowIndex:0,
			circleBig:false,
			input:'',
			records:[],
			total:0
		}
	},
	mounted() {
		let that=this
		emulation({name:''}).then(res=>{
			that.records=res.data.records
			that.total=res.data.total
		})
	},
	methods:{
		select(item,index){
			this.nowIndex=index
		},
		showC(){
			this.circleBig=true
		},
		change(val){
			return parseTime(val)
		},
		dow(item){
			dowlond(item).then(res=>{
				dowl(res,item)
			})
		},
		search(){
			let that=this
			emulation({name:this.input}).then(res=>{
				that.records=res.data.records
				that.total=res.data.total
			})
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/trainingPC.css';
</style>



